import styled from 'styled-components'

const ButtonCategory = styled.button`
    border: none;
    margin-right: 20px;
    color: black;
    background-color: transparent;
    font-size: 1rem;
    

    :hover{
        color: #fa4a0c;
        cursor: pointer;
        text-underline-position: under;
        text-decoration: underline;
    }
`

export {ButtonCategory}